.p-menubar {
    z-index: 1000; /* Adjust as necessary */
    position: relative; /* Ensure positioning context */
}

/* Set z-index for dropdown menus */
.p-menubar .p-menu {
    z-index: 1050; /* Ensure dropdowns are above the menubar */
}

/* Optional: z-index for submenu items */
.p-menubar .p-submenu {
    z-index: 1060; /* Ensure submenus are above parent menus */
}